//ProximaNova fonts
@include font("ProximaNova-Extra", proxima-extrabold);
@include font("ProximaNova-Bold", proxima-bold-webfont);
@include font("ProximaNova-SemiBold", proxima-semibold-webfont);
@include font("ProximaNova-Regular", proxima-regular-webfont);

@mixin extra {
	font-family: "ProximaNova-Extra", Arial, sans-serif; }
@mixin bold {
	font-family: "ProximaNova-Bold", Arial, sans-serif; }
@mixin semi {
	font-family: "ProximaNova-SemiBold", Arial, sans-serif; }
@mixin reg {
	font-family: "ProximaNova-Regular", Arial, sans-serif; }
