@function em($size, $context: 16) {
  @return ($size/$context) * 1em; }

@function img-url($image) {
  @return url('../img/#{$image}'); }


/* BEGIN functions for fluid font sizes  */
@function strip-unit($number) {
  @if type-of($number) == 'number' and not unitless($number) {
    @return $number / ($number * 0 + 1); }

  @return $number; }

@function calcFluidFontSize($f-min, $f-max, $w-min, $w-max, $units: px) {
  $f-min: strip-unit($f-min);
  $f-max: strip-unit($f-max);
  $w-min: strip-unit($w-min);
  $w-max: strip-unit($w-max);

  $k: ($f-max - $f-min)/($w-max - $w-min);
  $b: $f-min - $k * $w-min;

  $b: $b + $units;

  @return calc( #{$k} * 100vw + #{$b} ); }

@mixin fluidFontSize($f-min, $f-max, $w-min, $w-max) {

    font-size: calcFluidFontSize($f-min, $f-max, $w-min, $w-max, px); }

/* END functions for fluid font sizes  */


@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin smooth-scroll {
  -webkit-overflow-scrolling: touch; }

@mixin smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin gpu {
  transform: translate3d(0,0,0); }

@mixin md {
  @include r(1439) {
    @content; } }

@mixin lsm {
  @include r(1023) {
    @content; } }

@mixin sm {
  @include r(767) {
    @content; } }

@mixin xs {
  @include r(599) {
    @content; } }

@mixin from-md {
  @include rmin($r-md + 1) {
    @content; } }

@mixin from-sm {
  @include rmin($r-sm + 1) {
    @content; } }

@mixin from-xs {
  @include rmin($r-xs + 1) {
    @content; } }


@mixin f($size: null, $line-height: null, $spacing: null) {
  @if $size != null {
    font-size: $size + px; }
  @if $line-height != null {
    line-height: (ceil(($line-height / $size)*100)/100); }
  @if $spacing != null {
    letter-spacing: ($spacing / $size) + em; } }

@mixin hover {
  .no-touch & {
    &:hover {
      @content; } } }

@mixin active {
  .no-touch & {
    &:active {
      @content; } }
  &:active {
    @content; } }

@mixin no-touch {
  .no-touch & {
    @content; }
  @content; }

@mixin t($what) {
  transition: $what 0.25s ease; }
