.form-subscr {
	position: relative;
	&:before {
		content: "";
		position: absolute;
		height: 200px;
		background: $grayLight;
		z-index: -1;
		width: 100%;
		@include md {
			height: 100%; } }

	&__inner {
		margin: 50px auto;
		padding: 20px 65px 20px;
		@include clr;
		@include md {
			padding: 20px; } }
	&__img {
		float: left;
		width: 330px;
		vertical-align: top;
		margin-left: -20px;
		img {
			width: 100%;
			margin-top: -80px; }
		@include md {
			width: 250px;
			img {
				margin-top: -10px; } }
		@include sm {
			display: none; } }
	&__title {
		margin-bottom: 20px; }
	&__descr {
		margin-bottom: 20px;
		@include f(16, 18);
		color: $black;
		@include semi; }
	&__body {
		overflow: hidden; }
	&__content {
		max-width: 420px; } }

.form-field {
	font-size: 0;
	.form-subscr__input,
	.form-subscr__btn, {
		display: inline-block;
		vertical-align: top;
		width: 50%;
		@include md {
			display: block;
			width: 100%; } }
	.form-subscr__input {
		@include md {
			margin-bottom: 20px; } }
	.form-subscr__btn {
		padding-left: 30px;
		@include md {
			padding-left: 0; } }
	input {
		width: 100%;
		border-bottom: 1px solid #CECECE;
		background: none;
		padding: 17px 0;
		@include extra;
		@include f(16, 16);
		letter-spacing: 2.6px;
		text-transform: uppercase;
		color: $grayMid;
		@include placeholder {
			@include f(13, 13); } }
	button {
		max-width: none; } }
