// typography
h1,
h3,
.title-h1,
.title-h3 {
    @include bold; }

h1,
.title-h1 {
    @include f(64, 70);
    color: $white;
    @include md {
        @include f(46, 52); }
    @include sm {
        @include f(24, 26); }
    &_grayDark {
        color: $grayDark; }
    &_mb {
        margin-bottom: 20px; }
    &_mb_mid {
        margin-bottom: 35px;
        @include sm {
            margin-bottom: 15px; } } }

h2,
.title-h2 {
    @include f(32, 40);
    @include semi;
    color: $white;
    @include md {
        @include f(20, 20); }
    &_grayDark {
        color: $grayDark; }
    &_red {
        color: $red; }
    &_mb {
        margin-bottom: 20px; } }

h3,
.title-h3 {
    @include f(24, 24);
    color: $white;
    @include t(color);
    &_grayDark {
        color: $grayDark; }
    &_red {
        color: $red; }

    @include md {
        @include f(20, 20); } }

.title-h3 {
    &.title-h3_white {
        color: $white; } }

p, .text {
    @include f(20, 35);
    @include reg;
    color: $black;
    @include sm {
        @include f(16, 24); }

    &_sm {
        @include f(16, 20);
        @include semi; }
    &_white {
        color: $white; }
    &_bold {
        @include bold; } }

p + h3 {
    margin-top: 20px; }

h3 + p {
    margin-top: 10px; }

p + p {
    margin-top: 20px; }

.text-red {
    color: $red; }

.tag,
.date {
    @include f(12, 12);
    @include extra;
    @include t(color);
    max-width: 150px;
    letter-spacing: 2.2px;
    color: $white;
    text-transform: uppercase;
    @include sm {
        @include f(10, 10);
        letter-spacing: 2px; }
    span {
        display: inline-block;
        vertical-align: middle;
        max-width: 100px;
        text-transform: uppercase; }
    &_grayMid {
        color: $grayMid; }
    &_grayDark {
        color: $grayDark; }
    &_red {
        color: $red; }
    &_lg {
        @include f(16, 16);
        @include bold;
        max-width: 350px;
        letter-spacing: 3.2px;
        text-transform: uppercase;
        span {
            max-width: 300px; }
        @include sm {
            @include f(10, 10);
            letter-spacing: 2px; } }
    &_mb {
        margin-bottom: 5px; } }

.nav-tag,
.rubric-title {
    display: inline-block;
    vertical-align: middle;
    @include f(16, 16);
    @include bold;
    @include t(color);
    letter-spacing: 3.2px;
    text-transform: uppercase;
    text-decoration: none;
    color: $black;
    @include hover {
        color: $red; }
    @include md {
        @include f(14, 14);
        letter-spacing: 1.2px; }
    @include sm {
        @include f(12, 12); } }

.rubric-title {
    // margin-bottom: 15px
    &_pad {
        padding: 0 15px;
        @include sm {
            padding: 0 10px; } }
    @include sm {
        margin-bottom: 10px; } }

.fluid-font-size {
  @include r(1440) {
    @include rmin(1024) {
        @include fluidFontSize(18px, 24px, 1024px, 1440px); } }
  @include md {
    @include rmin(800) {
        @include fluidFontSize(11px, 18px, 480px, 1023px); } }
  @include r(799) {
    @include rmin(768) {
        @include fluidFontSize(10px, 18px, 480px, 1023px); } }
  @include r(767) {
    @include rmin(320) {
        @include fluidFontSize(16px, 18px, 480px, 1023px); } } }
