html, body {
  height: 100%!important;
  min-height: 100%!important; }

body {
  background: #FFF;
  font-size: 16px;
  line-height: 1.4;
  @include reg;
  color: $black; }

.inner {
  position: relative;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 15px;
  @include md {
    max-width: 970px; }
  @include sm {
    padding: 0 20px; } }

.out {
  padding-top: 105px;
  overflow: hidden;
  @include transition(padding-top .2s linear);
  @include md {
    padding-top: 75px; }
  @include r(1023) {
    padding-top: 110px; }
  @include sm {
    padding-top: 120px; }
  &.header-small {
    padding-top: 90px; } }

.hidden, [hidden] {
  display: none; }

.hide-md {
  @include md {
    display: none!important; } }

.hide-sm {
  @include sm {
    display: none!important; } }

.hide-xs {
  @include xs {
    display: none!important; } }

.show-sm {
  @include from-sm {
    display: none!important; } }

.show-md {
  @include from-md {
    display: none!important; } }
