// footer
.footer {
	height: 120px;
	line-height: 120px;
	@include clr;
	text-align: center;
	border-top: 1px solid $border;
	@include md {
		height: 90px;
		line-height: 90px; }
	@include r(1023) {
		height: auto;
		line-height: 1;
		margin: 0 -15px; }
	@include sm {
		margin: 0 -20px;
		text-align: left; } }

.footer-section {
	@include r(1023) {
		padding: 15px 0;
		&:not(:last-child) {
			border-bottom: 1px solid $border; } }
	@include sm {
		&:last-child {
			padding-left: 40px; } }
	&_left {
		float: left;
		width: 310px;
		text-align: left;
		img {
			vertical-align: middle; }
		@include md {
			width: 240px; }
		@include r(1023) {
			text-align: center;
			float: none;
			width: auto; }
		@include sm {
			text-align: left; } }
	&_right {
		float: right;
		max-width: 170px;
		@include r(1023) {
			text-align: center;
			float: none;
			max-width: none; }
		@include sm {
			.social {
				padding: 3px 35px; } } }
	&_center {
		display: inline-block;
		vertical-align: middle;
		@include r(1023) {
			display: block; } } }
